:root{
    --url: "ds";
  }
  .contenedor {
    background-color: black;
    padding: 20px 30px 30px;
    border-radius: 10px;
    width: 85%;
    margin: 5vh auto;
    color: white;
  }
  
  .contenedor h2 {
    margin: 0 0 20px 0;
    font-size: 1.3rem;
  }
  
  .input-convert {
    display: grid;
    gap: 10px;
    grid: 40px / 3fr 1fr 3fr;
  }
  
  .contenedor input {
    position: absolute;
    border-radius: 5px;
    width: 100%;
    appearance: none;
    outline: 0;
    border: 0;
    box-shadow: none;
    padding: 12px 20px;
    color: #767676;
    background-color: #ffffff;
    cursor: pointer;
    font-size: 1.2rem;
    height: 100%;
  }
  .contenedor input::-webkit-inner-spin-button{
    appearance: none;
  }
  .contenedor select {
    position: absolute;
    width: 100%;
    height: 100%;
    appearance: none;
    outline: 0;
    border: 0;
    box-shadow: none;
    color: #767676;
    background-color: #ffffff;
    cursor: pointer;
    font-weight: bold;
    text-transform: uppercase;
    inset: 0;
    margin: auto;
    font-size: 1.4rem;
    padding-left: 55px;
  }
  
  option{
    font-size: .6rem;
  }
  select::-ms-expand {
    display: none;
  }
  .select {
    position: absolute;
    display: flex;
    width: 40%;
    height: 100%;
    border-radius: 5px;
    inset: 0;
    margin:auto;
    margin-right: 0;
  }
  .input{
    position: relative;
    width: 100%;
  }
  .select img{
    position: absolute;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    inset: 0;
    margin: auto;
    margin-left: 15px;
    z-index: 10;
  }
  .select::after {
    content: "\25BC";
    position: absolute;
    width: 15px;
    height: 50%;
    inset: 0;
    margin: auto;
    margin-right: 20px;
    transition: 0.25s all ease;
    pointer-events: none;
    color: #767676;
    display: grid;
    place-items: center;
  }
  
  .select:hover::after {
    color: #02735e;
  }
  
  .icono {
    place-self: center;
  }